	.navbar-trigger {
		display: block;
		width: 30px;
		height: 30px;
		position: relative;
		margin: 11px 0 0 0;
		cursor: pointer;
	}

	.navbar-trigger span {
		position: absolute;
		transition: all .2s ease;
		height: 2px;
		background: #fff;
		left: 0;
		right: 0;
	}

	.navbar-trigger span:nth-child(1) {
		top: 2px;
	}

	.navbar-trigger span:nth-child(2) {
		top: 50%;
		margin-top: -1px;
	}

	.navbar-trigger span:nth-child(3) {
		bottom: 2px;
	}

	.navbar-trigger.active span:nth-child(1) {
		top: 50%;
		margin-top: -1px;
		transform: rotate(-45deg);
	}

	.navbar-trigger.active span:nth-child(2) {
		opacity: 0;
	}

	.navbar-trigger.active span:nth-child(3) {
		bottom: 50%;
		margin-bottom: -1px;
		transform: rotate(45deg);
	}

	.navbar-wrapper {
		display: none;
		position: absolute;
		top: 100%;
		right: 0;
		background: #101B51;
		padding: 20px 30px;
		box-shadow: 0px 4px 55px rgba(0, 0, 0, 0.1);
