	}

	.struct-bottom .col:before {
		content: "";
		position: absolute;
		left: -32px;
		top: -2px;
		bottom: -12px;
		width: 2px;
		background: #fff;
	}

	.struct-bottom .col:last-child:before {
		bottom: 50%;
	}
	
	.struct-bottom .col:after {
		content: "";
		width: 30px;
		right: 100%;
		top: 50%;
		margin-left: -2px;
		height: 2px;
		margin-right: 2px;
		background: #fff;
		position: absolute;
	}

