html,
body {
	font-family: 'Montserrat', sans-serif;
	font-weight: 500;
	font-size: 16px;
	line-height: 1.5;
	color: #fff;
	background: #101B51;
	min-width: 360px;
}

p {
	margin: 0 0 15px 0;
}

p:last-child {
	margin: 0;
}

a,
a:focus {
	color: #fff;
	text-decoration: underline;
	transition: color .15s ease;
}

a:hover {
	text-decoration: none;
	color: #3B71DB;
}

.section-page-content a,
.section-page-content a:focus {
	color: $blue-color;
}

h1,
.h1 {
	font-size: 75px;
	line-height: 0.9;
	font-weight: 600;
	margin: 0 0 36px 0;
	color: #fff;
	letter-spacing: -0.01em;
}

h2,
.h2 {
	font-weight: bold;
	font-size: 40px;
	line-height: 1.2;
	color: #fff;
	margin: 50px 0 26px 0;
}

h3,
.h3 {
	font-weight: 600;
	font-size: 35px;
	line-height: 1.2;
	color: #fff;
	margin: 50px 0 22px 0;
}

.over-wrapper ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

.over-wrapper ul:not([class]) {
	list-style: none;
	margin: 0 0 15px 0;
	padding: 0;
}

.over-wrapper ul:not([class]) > li {
	margin: 0 0 15px 0;
	padding: 0 0 0 65px;
	position: relative;
}

.over-wrapper ul:not([class]) > li:before {
	content: "";
	border: 1px solid #2C57AB;;
	width: 15px;
	height: 15px;
	position: absolute;
	left: 0;
	top: 4px;
	border-radius: 50%;
}

.over-wrapper ol:not([class]) {
	list-style: none;
	margin: 0;
	padding: 0;
	counter-reset: item;
}

.over-wrapper ol:not([class]) > li {
	margin: 0 0 15px 0;
	padding: 0 0 0 45px;
	position: relative;
}

.over-wrapper ol:not([class]) > li:before {
	counter-increment: item;
	content: counter(item);
	position: absolute;
	left: 0;
	top: 0;
	font-weight: normal;
	color: #2C57AB;
}

.over-wrapper ol:not([class]) > li:nth-child(-n+10):before {
	content: "0" counter(item);
}




//  END

// Buttons

.btn,
.btn:focus {
	-webkit-box-shadow: none;
	-moz-box-shadow: none;
	box-shadow: none;
	background-color: transparent;
	text-decoration: none !important;
	-webkit-border-radius: 0;
	-moz-border-radius: 0;
	border-radius: 0;
	vertical-align: top;
	border: none;
	position: relative;
	display: inline-block;
	vertical-align: middle;
	-webkit-box-shadow: none;
	-moz-box-shadow: none;
	box-shadow: none;
	text-align: center;
	transition: background-color .2s ease, color .2s ease, box-shadow .2s ease, border-color .2s ease, box-shadow .2s ease, font-size .2s ease;
	-webkit-appearance: none !important;
}

.btn-1,
.btn-1:focus {
	font-size: 18px;
	line-height: 1.2;
	font-weight: 600;
	color: #fff !important;
