// Sliders

.slick-arrow {
	position: absolute;
	z-index: 10;
	border: none;
	-webkit-appearance: none;
	width: 50px;
	height: 100px;
	background: #00D2FD;
	transition: all .3s ease;
	font-size: 0;
	line-height: 0;
	color: transparent;
	text-indent: -20000px;
	overflow: hidden;
	top: 50%;
	margin-top: -50px;
	cursor: pointer;
	outline: none !important;
}

.slick-arrow:hover {
	background: #D7DC43;
}

.slick-arrow.slick-disabled {
	opacity: .2 !important;
}

.slick-arrow:before {
	content: "";
	width: 17px;
	height: 26px;
	background-repeat: no-repeat;
	background-position: 0 0;
	position: absolute;
	left: 50%;
	top: 50%;
	margin: -13px 0 0 0;
}

.slick-prev:before {
	left: 24px;
	background-image: url(../images/slick-prev.svg);
}

.slick-next:before {
	left: auto;
	right: 24px;
	background-image: url(../images/slick-next.svg);
}

.slick-prev {
	border-radius: 50px 0 0 50px;
	right: 15px;
}

.slick-next {
	border-radius: 0 50px 50px 0;
	right: -35px;
}

.slider-wrapper {
	margin: 0 -15px;
	position: relative;
}

.slick-slide {
	margin: 0 15px;
}

.slick-slide img {
	max-width: 100%;
}

.news-slider .slick-arrow {
	top: 160px;
}

.slider-countable-wrapper {
	padding-bottom: 65px;
}

.photo-slider-wrapper {
	margin-left: 0;
	margin-right: 0;
	margin-top: 36px;
	margin-bottom: 40px;
}

.photo-slider .slick-slide {
	margin: 0;
}

.slider-countable .slick-arrow {
	width: 45px;
	height: 45px;
	border-radius: 50%;
	top: auto;
	right: auto;
	bottom: -65px;
}

.slider-countable .slick-prev {
	left: 0;
}

.slider-countable .slick-next {
	left: 210px;
}

.slider-countable .slick-arrow:before {
	width: 11px;
	height: 16px;
	margin-top: -8px;
}

.slider-countable .slick-prev:before {
	left: 15px;
	background-image: url(../images/slick-prev-countable.svg);
}

.slider-countable .slick-next:before {
	right: 15px;
	background-image: url(../images/slick-next-countable.svg);
}

.slider-count {
	font-size: 20px;
	font-weight: 300;
	position: absolute;
	left: 0;
	bottom: 6px;
	text-align: center;
	width: 255px;
}

.slider-count-cur {
	font-weight: 800;
}

.slider-count-sep {
	margin: 0 5px;
}

// Sliders END

// Modals

.modal {
	color: #000;
}

.modal-backdrop {
	background: rgb(0, 14, 58);
}

.modal-backdrop.show {
	opacity: .8;
}

.modal-dialog {
	pointer-events: auto;
	width: auto;
	margin: 0 auto;
	display: flex;
	min-height: 100%;
	flex-direction: column;
	justify-content: center;
}

.form-modal .modal-dialog {
	max-width: 910px;
	padding: 90px;
}

.modal-content {
	background: none;
	border: none;
	border-radius: 0;
	position: relative;
}

.modal-header {
	display: block;
	border: none;
	padding: 0;
	margin: 0 0 32px 0;
}

.modal-header h2,
.modal-header .h2 {
	margin: 0;
	font-size: 45px;
	color: #000;
	margin: 0;
	text-transform: none;
	font-weight: 600;
	letter-spacing: -0.01em;
}

.form-modal .modal-header-text {
	font-weight: bold;
	margin: 12px 0 0 0;
	max-width: 560px;
}

.modal .close {
	position: absolute;
	right: 30px;
	top: 30px;
	opacity: 1 !important;
	width: 32px;
	height: 32px;
	opacity: 1 !important;
	cursor: pointer;
	z-index: 10;
	border-radius: 50%;
	outline: none !important;
	transition: all .2s ease;
}

.form-modal .close {
	right: -60px;
	top: -60px;
}

.side-modal .close {
	right: 30px;
	top: 30px;
}


.modal .close:before {
	content: "";
	background: url(../images/close-modal.svg) 0 0 no-repeat;
	width: 32px;
	height: 32px;
	position: absolute;
	left: 0px;
	top: 0px;
	transition: all .2s ease;
}

.modal-body {
	padding: 0;
}

.side-modal {
	padding-right: 90px !important;
	padding-left: 0 !important;
}

.side-modal .modal-dialog {
	background: #fff;
	margin-right: auto;
	margin-left: 0;
	max-width: 1066px;
	padding: 0;
}

.side-modal .modal-content {
	padding: 87px 95px 107px 95px;
}

.side-modal .modal-content-inner {
	margin-left: auto;
	max-width: 825px;
}

.side-modal .close {
	right: 30px;
	top: 30px;
}

.side-modal .modal-header {
	margin: 0 0 31px 0;
}

.form-modal .modal-content {
	background: #fff;
	padding: 68px 65px 60px 65px;
}

// Modals END

